

:root{

    --purple: #5267DF;
	  --red: #FA5959;
		 --blue :#242A45;
		 --grey:#9194A2;
		 --white : #f7f7f7;
		 --rose-700: #BE123C;
		 --green:#21bd9d;

}




.table{
 margin-bottom: 1em;


}


table {
    border-collapse: collapse;
    margin-top: 10px;
    width: 100%;
   
    }

    table th {
        text-align: center;
        background-color: #3e6070;
        color: #fff;
        padding: 4px 30px 4px 2px;
    }

    table td {
        border: 1px solid #e3e3e3;
        padding: 4px 8px;
    }

    table tr:nth-child(odd) td {
        background-color: #e7edf0;
    }




#app{
   

  

}

.even {
    display: flex;
    


}

.even > * {
   flex-basis: 100%;

}


.grid-ish{

    display: flex;
    flex-wrap: wrap;

}


.grid-ish > * {
    flex: 1 1 10em;

}


.side-bar {
    display: flex;
    

}

.side-bar > *:nth-child(2){
    flex: 1 1 70%;
    

}


.side-bar > *:nth-child(1){
    flex: 1 1 20%;
    max-width: 20%;

}





.btn{
    text-decoration: none;
    padding:10px 40px;
    position: relative;
    color: #fff;
    text-transform: uppercase;
   
    
   
    

}


.btn-1{
    background: lightblue;
    color: #fff;
    border-radius: 30px;
    box-shadow: 0 0 0 0 rgba(143, 64, 248, .5), 0 0 0 0 rgba(39, 200, 255, .5);
    transition: transform .3s ease, box-shadow .3s ease;
  
  
}



